import React, { Fragment } from 'react'

const CubAtual = () => {

    const mes = 'Setembro/2019'
    const resValor = 'R$ 0.000,00'
    const resPerc = '0,00%'
    const comValor = 'R$ 0.000,00'
    const comPerc = '0,00%'

    return (
        <div style={gStyle}>
            <div style={tStyle}>CUB do mês de {mes} </div>
            <div style={pStyle}>
                <div>Residencial : {resValor} - {resPerc}</div>
                <div>Comercial : {resValor} - {resPerc}</div>

            </div>
        </div>
    )
}

const gStyle = {
    display: 'flex',
    flexDirection: 'column',
    width: '95%',
    fontWeight: 'bold',
    color: '#686868'
}

const tStyle = {
    display: 'flex',
    justifyContent: 'center',
    alignItems: 'center',
    // width: '70%',
    fontSize: '20px',
    // fontWeight: 'bold',
    // color: 'darkgrey'
}

const pStyle = {
    display: 'flex',
    justifyContent: 'space-around',
    alignItems: 'center',
    // width: '70%',
    borderTop: '1px solid darkgrey',
    padding: '5px 1px 5px 10px',
    borderBottom: '1px solid darkgrey',
    fontSize: '15px',
    // fontWeight: 'bold',

    // color: 'darkgrey'
}

export default CubAtual