import React, { Fragment } from 'react'

import IntranetHeader from './IntranetHeader'
import CubAtual from './CubAtual'
import Ramais from './Ramais'
import MensagensLista from './MensagensLista'
import Empreendimentos from './Empreendimentos'
import '../css/intranet.css'

const Intranet = () => {
    return (
        <div className="intra-container">
            <IntranetHeader/>

            <div style={lin01Style}>
                <div style={{ flex: 8 }}>
                    <CubAtual />
                </div>
                <div style={{ flex: 2 }}>

                    <Ramais style={{ alignItems: 'center' }} />
                </div>
            </div>

            <div style={lin01Style}>
                <div className="intra-flex7 intra-flex-center">

                    <MensagensLista />
                </div>
                <div className="intra-flex3 intra-flex-center">

                    <Empreendimentos />
                </div>
            </div>


        </div>
    )
}



const lin01Style = {
    display: 'flex',
    width: '100%'
}

export default Intranet